import React, { Component } from "react";

import { DeckGL } from "@deck.gl/react";
import { MapController, MapView } from "@deck.gl/core";
import { Tile3DLayer, TileLayer } from "@deck.gl/geo-layers";
import { Tiles3DLoader } from "@loaders.gl/3d-tiles";
import { BitmapLayer } from "@deck.gl/layers";

import {
    LightingEffect,
    AmbientLight,
    _SunLight as SunLight
} from "@deck.gl/core";

const ambientLight = new AmbientLight({
    color: [255, 250, 250],
    intensity: 4.1
});

const dirLight = new SunLight({
    timestamp: Date.UTC(2022, 7, 1, 12),
    color: [255, 250, 250],
    intensity: 4.1,
    _shadow: false
});

const lightingEffect = new LightingEffect({ ambientLight, dirLight });

const INITIAL_VIEW_STATE = {
    longitude: 5,
    latitude: 52,
    pitch: 0,
    maxPitch: 85,
    bearing: 0,
    minZoom: 2,
    maxZoom: 30,
    zoom: 18
};

const view = new MapView({
    repeat: false,
    nearZMultiplier: 0.01,
});

export default class App extends Component {
   
    constructor(props) {
        super(props);

        this.state = {
            viewState: INITIAL_VIEW_STATE,
        };

        this.effects = [lightingEffect];
    }

    _onTilesetLoad(tileset) {
        console.log(tileset);

        const { zoom, cartographicCenter } = tileset;
        const [longitude, latitude] = cartographicCenter;

        const viewState = {
            ...this.state.viewState,
            zoom: zoom + 2.5,
            longitude,
            latitude,
        };

        this.setState({
            viewState: {
                ...viewState,
            }
        });

        tileset.setProps({maximumScreenSpaceError: 1});
    }

    _onTileLoad(tileHeader) {
        console.log(tileHeader);
    }

    _renderLayers() {
        return [
            new Tile3DLayer({
                id: "tile-3d-layer",
                pointSize: 0.5,

                loader: Tiles3DLoader,
                data : "tileset.json",

                onTilesetLoad: this._onTilesetLoad.bind(this),
                onTileLoad: this._onTileLoad.bind(this),
            }),

            new TileLayer({
                id: "tile-layer",
                data: "https://c.tile.openstreetmap.org/{z}/{x}/{y}.png",
                minZoom: 0,
                maxZoom: 19,
                tileSize: 256,

                renderSubLayers: (props) => {
                    const {
                        bbox: {
                            west,
                            south,
                            east,
                            north
                        },
                    } = props.tile;

                    return new BitmapLayer(props, {
                        data: null,
                        image: props.data,
                        bounds: [west, south, east, north],
                    });
                },
            }),
        ];
    }

    render() {
        const { viewState } = this.state;

        return (
            <div
                onContextMenu={(e) => e.preventDefault()}
                onMouseDown={(e) => e.preventDefault()}
            >
            <DeckGL {...{ orthographic: true, fovy: 40, focalDistance: 1, padding: null, near: 30, far: 200 }}
                layers={this._renderLayers()}
                effects={this.effects}
                viewState={viewState}
                controller={{ type: MapController, touchRotate: true }}
                views={[view]}
                onViewStateChange={({ viewState }) => {
                    this.setState({ viewState });
                }}
                onClick={this._onClick}
            >

            </DeckGL >
            </div>            
        );
    }
}
